import fetch from 'isomorphic-unfetch'

function BestJokes({stars}) {


  return (
    <div>
      <p>This is the about page</p>
      <div>Next stars: {stars}</div>
      <ul>
        { stars.map( (star)  => { return <li key={star.id}>{star.joke}</li> }) }
      </ul>
    </div>
  );
  
}

BestJokes.getInitialProps = async ({ req }) => {
  debugger
  const res = await fetch('http://api.icndb.com/jokes/random/3')
  const json = await res.json()

  return { stars: json.value }
  // return { stars: json.value.joke }
}

export default BestJokes